import { Component, OnInit, ChangeDetectorRef } from '@angular/core';
import { Subscription } from 'rxjs';
import { UserTableListComponent } from '../user-table-list/user-table-list.component';
import { Router, ActivatedRoute } from '@angular/router';
import { SessionService } from 'src/app/core/session/session.service';
import { SharedService } from 'src/app/shared/services/shared.service';
import { LookupService } from 'src/app/api/controllers/Lookup';
import { DocumentService } from 'src/app/api/controllers/Document';

@Component({
  selector: 'app-user-upload-drawer',
  templateUrl: './user-upload-drawer.component.html',
  styleUrls: ['./user-upload-drawer.component.scss']
})
export class UserUploadDrawerComponent implements OnInit {
  document:any = {
    UserDocumentName:''
  };
  urlType:string;
  documentCategoryList:any = [];
  fileUploadList:any = [{FileDetailsId:null}];
  apiUnsubcribe:Subscription;
  constructor(
    private sharedService:SharedService,
    private activeRouter: ActivatedRoute,
    private sessionService:SessionService,
    private changeDetectorRef: ChangeDetectorRef,
    private router: Router,
    private lookupService: LookupService,
    private userTableListComponent : UserTableListComponent,
    private documentService:DocumentService,
  ) { }

  goBack() {
    this.userTableListComponent.matDrawer.close();
    this.router.navigate(['.'], {relativeTo: this.activeRouter.parent});
  }

  setDocCategory(event) {
    this.document.UserDocumentCategoryId = event[0].lookupValueId
  }

  getDocumentCategoryList() {
		let params = {
			LookupTypeId: 62
		}
		this.lookupService.getLookupValueByLookupTypeId(params).subscribe((res:any) => {
			this.documentCategoryList = res;
		});
  }

  getFileId(event,data) {
    data.FileDetailsId = event;
  }

  addFileUpload() {
    this.fileUploadList.push({FileDetailsId:null});
  }

  deleteFileUpload(index) {
    this.fileUploadList.splice(index,1);
  }
  
  createDocument() {
    this.fileUploadList.forEach((data)=>{
      if(data.FileDetailsId) {
        let uploadDetails = {
          "ApartmentId" : this.sessionService.apartmentId,
          "ApartmentBlockUnitId" : 102,
          "UserDocumentCategoryId" : this.document.UserDocumentCategoryId,
          "UserDocumentTypeId" : 1,
          "UserDocumentAccessibleToId" : 1,
          "UserId" : this.document.UserId,
          "UserDocumentName" : this.document.UserDocumentName,
          "Description" : this.document.Description,
          "IsActive":true,
          "InsertedBy": this.sessionService.userId,
          "InsertedOn": new Date().toISOString(),
          "UpdatedBy":null,
          "UpdatedOn":null,
          "FileDetailsId":data.FileDetailsId,
          "IsPublic" : true,
          "files" : null,
        }
        this.documentService.addUserDocument(uploadDetails).subscribe((res:any) => { 
        },
        error => {
          console.log(error)
        }); 
      }
    })
  }

  ngOnInit(): void {
     //Open the drawer
    this.apiUnsubcribe = this.sharedService.matdraweridcast.subscribe(id => {
      if(id != null && id == this.activeRouter.params['value'].id) {
        this.urlType = this.activeRouter.params['value'].type;
        this.document.UserId = id;
        this.userTableListComponent.matDrawer.open();
        this.getDocumentCategoryList();
      }
      //Mark for check
      this.changeDetectorRef.markForCheck();
    })
  }

  ngOnDestroy() {
    this.apiUnsubcribe.unsubscribe();
  }

}
